<template>
  <!--  -->
  <div class="h100">
    <div>
      <el-row>
        <el-col :span="12">
          <el-date-picker size="mini"
            format="yyyy/MM" value-format="yyyy/MM"
            v-model="month" type="month" placeholder="">
          </el-date-picker>
          <el-button class="ml10" type="primary" size="mini" @click="search"></el-button>
        </el-col>
      </el-row>
    </div>
    <div class="detail h100" v-loading="loading">
      <Table 
        style="height: calc(100% - 50px);"
        :header="headerData" 
        :data="data" 
        size="mini"
        :operation="operation"
        @operateClick="operateClick"
        :showPage="false"
      ></Table>
    </div>
  </div>
</template>

<script>
import { mapActions, mapState } from 'vuex'
import Table from '../../components/table.vue'
import Config from '../../config/common.json'

export default {
  name: 'EpidemicCollectManager',
  components: {
    Table
  },
  computed: {
    ...mapState({
      unitList: 'unit',
    }),
  },
  data() {
    return {
      loading: false,
      month: (new Date()).getFullYear() + '/' + ((new Date()).getMonth()+1),
      curMonth: '',
      headerData: [
        { key: 'day', label: '' },
        { key: 'zfile', label: '', type: 'link' },
        { key: 'sfile', label: '', type: 'link' },
      ],
      data: [],
      operation: [
        { title: '', type: 'create' }
      ],
      downloadurl: Config.server + '/epidemic/statistics/download?code=',
      // 
      type: '',
      // 
      url: {
        EpidemicCollectManager: {
          search: '/epidemic/statistics/select',
          create: '/epidemic/statistics/create'
        }
      }
    }
  },
  methods: {
    ...mapActions([
      'unit'
    ]),
    operateClick(opt) {
      console.log(opt)
      this.currUnit = opt.data || {}
      switch(opt.type) {
        case 'create': {
          console.log(opt)
          this.createemployeecard(opt.index+1)
          break
        }
      }
    },
    // 
    search() {
      if (!this.month) {
        this.$message({
          showClose: true,
          message: '',
          type: 'error'
        });
        return
      }
      this.loading = true
      this.request.post(this.url[this.type].search, {
        date: this.month
      }).then(res => {
        this.curMonth = this.month
        this.loading = false
        if (res.status == 200 & res.data.success) {
          this.data = res.data.data || []
          this.data.forEach((item, index) => {
            item.day = (index + 1) + ' ',
            item.zfile = {
              name: item.zhengGongFileName,
              url: this.downloadurl + item.zhengGongFileCode,
              message: item.message || ''
            }
            item.sfile = {
              name: item.shiJuFileName,
              url: this.downloadurl + item.shiJuFileCode,
              message: item.message || ''
            }
          })
        } else {
          this.$message({
            showClose: true,
            message: res.data.message,
            type: 'error'
          });
        }
      }) 
    },
    // 
    createemployeecard(day) {
      this.loading = true
      console.log(this.curMonth + '/' + day)
      this.request.post(this.url[this.type].create, {
        date: this.curMonth + '/' + day,
      }).then(res => {
        this.loading = false
        if (res.status == 200 & res.data.success) {
          if (res.data.data) {
            this.$message({
              showClose: true,
              message: res.data.data,
              type: 'warn'
            });
          } else {
            this.$message({
              showClose: true,
              message: '',
              type: 'success'
            });
            this.search()
          }
        } else {
          this.$message({
            showClose: true,
            message: res.data.message,
            type: 'error'
          });
        }
      }) 
    },
    // 
    init() {
      if (this.$route.path == '/EpidemicManager/EpidemicCollectManager') {
        this.type = 'EpidemicCollectManager'
      }
      this.search()
    }
  },
  watch: {
    $route(val) {
      if (val) this.init()
    }
  },
  mounted() {
    this.init()
  }
}
</script>

<style scoped>
.h100 {
  height: 100%;
}
.ml10 {
  margin-left: 10px;
}
.detail {
  margin-top: 20px;
}
</style>
